.region-content-two .block {
  margin-bottom: theme("margin.8"); }

@screen md {
  .region-content-two .block {
    padding-left: theme("padding.8");
    padding-right: theme("padding.8");
    margin-bottom: 0; }
    .region-content-two .block:first-child {
      padding-right: theme("padding.4"); }
    .region-content-two .block:last-child {
      padding-left: theme("padding.4"); } }
